@use "../utils/variables";

.error404 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1 {
        font-size: 290px;
        margin: 0;
    }

    a {
        margin: 140px 0;
        color: variables.$primary;
        text-decoration: underline;

    }
}

@media screen and (max-width: 992px) {
    .error404 {
    h1 {
        font-size: 96px;
        margin: revert;
    }

    h2 {
        font-size: 18px;
        text-align: center;
    }

    a {
        font-size: 14px;
    }
 }
}